
.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 40vmin;
  pointer-events: none;
  display: inline-block;
  float: left;
  width:200px;
  height: 200px;
  margin-left: 200px;
}

.headLine{
  padding: 50px;
  margin-left:50px;
  text-align: center;
  display:inline-block;
}

.App {
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
